@import "./components/header";
@import "./components/footer";
@import "_mixins";




.btn {
    @include main-btn;

    &-third {
        background-color: white;
        color: $color-secondary;
        width: 100%;
        max-width: 319px;
        border: 2px solid $color-secondary;
        @include fluidFontSize(12px, 16px, 320px, 1920px, 18px);
        @include fluidLineHeight(14px, 19px, 320px, 1920px, 18px);

        @media (max-width: 767px ) {
            max-width: 300px;
            padding: 1.5em 3.3em;
        }

        &:hover {
            color: white;
            background-color: $color-secondary;
            
        }
    }
    &-secondary {
        font-family: 'Cochin', sans-serif;
        font-style: italic;
        // @include fluidFontSize(30px, 30px, 768px, 1920px, 18px);
        // @include fluidLineHeight(17px, 33px, 768px, 1920px, 18px);
        font-size: 30px;
        line-height: 33px;
        @include font-main-light;
        color: $color-secondary;
        background: #FFFFFF;
        box-shadow: 0px 4px 30px rgba(51, 51, 51, 0.1);
        padding: 10px 53px 12px 30px;
        letter-spacing:1px;
        position: relative;
        transition: $transition;
        cursor: pointer;
        z-index: 10;
        
        &:hover {
            transform: scale(1.1);
        box-shadow: 0px 4px 30px rgba(51, 51, 51, 0.2);

        }


        &.__gallery {
      
        }
      
        &::before {
          position: absolute;
          content: '';
          background: url('../img/arrow.svg') center center no-repeat;
          background-size: cover;
          width: 30px;
          height: 30px;
          right: 10px;
          top: 50%;
          transform: translateY(-50%);
        }
      }
   
    &.btn-intro {
        max-width: 275px;
        @include fluidFontSize(13px, 16px, 320px, 1920px, 18px);
        @include fluidLineHeight(17px, 19px, 320px, 1920px, 18px);
    }

    &-header {
        border: 2px solid $color-intro;
        background: rgba(255, 255, 255, 0.2);
        backdrop-filter: blur(8px);
        @include fluidFontSize(11px, 16px, 320px, 1920px, 18px);
        @include fluidLineHeight(14px, 19px, 320px, 1920px, 18px);
        

        @media (max-width: 1679px) {
            padding: 1em 2em;
        }

        @media (max-width: 1280px) {
            padding: 1em 2em;
        }

        &:hover {
            background: $color-intro;
            color: $color-main;
        
            
        }
    }

    &-main {
        background-color: $color-secondary;
        color: $color-intro;
        width: 100%;
        max-width: 319px;
        @include fluidFontSize(12px, 16px, 320px, 1920px, 18px);
        @include fluidLineHeight(14px, 19px, 320px, 1920px, 18px);
        border: 2px solid transparent;


        @media (max-width: 767px ) {
            max-width: 300px;
            padding: 1.5em 3.3em;
        }

        &:hover {
            color: $color-secondary;
            background-color: white;
        border: 2px solid $color-secondary;
            
            
        }
    }

    &-order {
        // border: none;
        max-width: 283px;
    }

    &-watch-video {
        border: 1px solid $color-secondary;
        border-radius: 12px;
        width: 351px;
        padding: 10px 30px;
        position: relative;
        transition: $transition;
        cursor: pointer;

        &:hover {
            transform: scale(1.1);
        }

        @media (max-width: 767px) {
            // width: 100%;
            margin: 0 auto;
        }

        @media (max-width: 374px) {
            width: 100%;
            // margin: 0 auto;
        }

        &::before {
            position: absolute;
            content: '';
            background: url('../img/video.svg') center center no-repeat;
            background-size: cover;
            width: 49px;
            height: 49px;
            right:24px;
            top: 50%;
            transform: translateY(-50%);
        }

        &.__routes-info {
            @media (max-width: 1440px) {
                margin-left: -25%;
            }
            @media (min-width: 768px) and (max-width: 991px) {
            margin-left: -28%;

            }

            @media (max-width: 991px) {
                margin-left: 0;
                width: 300px;
                margin-top: 5%;
            }

            &::before {
                
                right:17px;
              
            }

            &.__mobile {
                @media (min-width: 768px) {
                    display: none;
                }
            }

            &.__desktop {
                @media (max-width: 767px) {
                    display: none;
                }
            }
        }

        &.__info-about-us {
            margin: 0 auto;
        }
        
        &__sub--indent {
            padding-left: 78px;
            @media (max-width: 767px) {
                padding-left: 94px;

             }

            &.__info-about-us {
                padding-left: 58px;
            }
        }

        &__text {
            @include fluidFontSize(18px, 22px, 320px, 1920px, 18px);
            @include fluidLineHeight(22px, 23px, 320px, 1920px, 18px);
            color: $color-main;
            margin-bottom: 3px;
            padding-top: 8px;
        }
    }

    &.month-btn {
        font-family: 'Cochin', sans-serif;
        @include font-main-light;
        font-style: italic;
        font-weight: 300;
        @include fluidFontSize(25px, 30px, 320px, 1920px, 18px);
        @include fluidLineHeight(28px, 33px, 320px, 1920px, 18px);
        letter-spacing: 2px;
        overflow-wrap: break-word;
        padding: 10px;
        text-transform: lowercase;
        margin-bottom: 27px;

        background-color: $color-secondary;
        color: $color-intro;
        width: 100%;

        border: 2px solid transparent;

        @media (max-width: 991px) {
            display: none;
         }
    }
}


