.main-section {
    width: 100%;
    position: relative;

    &__inner {
        &.__routes-info {
            padding-top: 55px;
            position: relative;

            @media (max-width: 991px) {
                // position: relative;
                padding-top: 53px;
             }
        }

        &.__nearest-trips {
            padding-top: 275px;
            position: relative;
            z-index: 20;

            @media (max-width: 991px) {
                padding-top: 100px;
             }
        }

        &.__our-team {
            display: flex;
            flex-direction: column;
            padding-top: 136px;

            @media (max-width: 991px) {
                padding-top: 42px;
             }
        }

        &.__gallery {
            padding-top: 180px;

            @media (max-width: 991px)  {
                padding-top: 64px;
             }
        }

        &.__trainee-benefits {
            display: flex;
            flex-direction: column;
            padding-top: 200px;
            padding-bottom: 63px;

            @media (max-width: 991px) {
                padding-top: 37px;
                padding-bottom: 45px;

            }

        }
        &.__reviews-form {
            display: flex;
            // padding-top: 200px;
            position: relative;

            @media (max-width:991px) {
                flex-direction: column;
            }

            input[type="text"] {
                @media (min-width: 992px) {
                    border: 1px solid $color-intro;
                    color: $color-intro;
                }
               

            }

        }


    }

    &__content {
        display: flex;
        flex-wrap: wrap;

        &.__nearest-trips {
            @media (max-width: 991px) {
                justify-content: center;
            }
        }

        &.__routes-info {
            justify-content: space-between;
        }

        &.__presentation-info {
            justify-content: space-between;
            margin-bottom: 116px;


            @media (max-width: 1199px) {
                margin-bottom: 60px;

            }

        }

       
    }

    &-title {
        @include fluidFontSize(16px, 25px, 320px, 1920px, 18px);
        @include fluidLineHeight(20px, 28px, 320px, 1920px, 18px);
        overflow-wrap: break-word;

        &.__main-title-indent {
            margin-bottom: 10px;

            @media (max-width: 991px) {
                margin-bottom: 1px;
                // margin-left: 10px;

            }
        }

        &.__reviews {
            text-align: center;
            @media (max-width: 991px) {
                text-align: start;
                margin-left: 7px;
            }
        }

        &.__video-reviews  {
            @include fluidFontSize(17px, 17px, 320px, 1920px, 18px);
            @include fluidLineHeight(23px, 23px, 320px, 1920px, 18px);
            overflow-wrap: break-word;
            @include font-main-regular;
            color: $color-main;

            @media (max-width: 991px) {
                margin-left: 0;
                margin-bottom: -7px;
    
                }
    
        }

    }

    &__title-wrapper {
        max-width: 665px;
        &.__routes-info {
            margin-bottom: 8%;
            @media (max-width: 1440px) {
                margin-bottom: 5%;
            }

             @media (max-width: 991px) {
                margin-bottom: 28px;
            }
        }

        &.__gallery {
            margin-bottom: 63px;
            @media (max-width: 767px) {
                margin-bottom: 28px;
            }
        }

        &.__our-team {
            padding-top: 10px;


            @media (max-width: 991px) {
                margin-bottom: 33px;
            }
        }

        &.__presentation-info {
            // margin-bottom: 106px;
            margin-top: -142px;
            margin-bottom: 37px;
            // max-width: 665px;

            @media (max-width: 1199px) {
                margin-top: 0;
            }

            @media (max-width: 767px) {
            margin-top: 0;

            }
        }

        &.__info-about-us {
            margin-bottom: 39px;
            padding-right: 95px;

            @media (max-width: 991px) {
                padding-right: 0;
                margin-bottom: 26px;
                padding: 0 15px;
            }

           
        }

        &.__video-reviews {
            margin-bottom: 39px;
            padding-right: 95px;

            @media (max-width: 991px) {
                margin-bottom: 26px;
                padding-right: 0;

            }
        }
    }

    &.__presentation-info {
        position: relative;
    }

   

}
.secondary-title {
    font-family: 'Cochin', sans-serif;
    @include font-main-light;
    font-style: italic;
    font-weight: 300;
    @include fluidFontSize(16px, 32px, 320px, 1920px, 18px);
    @include fluidLineHeight(20px, 33px, 320px, 1920px, 18px);
    letter-spacing: 2px;
    overflow-wrap: break-word;

    &.__reviews {
        text-align: center;
        @media (max-width:991px) {
            text-align: start;
        }
    }
    
   

    &.__our-team-members {
        @include fluidFontSize(29px, 32px, 320px, 1920px, 18px);
    @include fluidLineHeight(30px, 33px, 320px, 1920px, 18px);
    }

    &.__video-reviews {
        @include fluidFontSize(22px, 22px, 320px, 1920px, 18px);
        @include fluidLineHeight(24px, 24px, 320px, 1920px, 18px);
        position: relative;

        &:before {
            position: absolute;
            content: "";
            background: url('../img/video.svg') center center no-repeat;
            background-size: cover;
            width: 25px;
            height: 25px;
            right: -58px;
            bottom: 0;

            @media (max-width: 991px) {
                right: 10px;
            }
        }
    }

    &.__routes-info {
        position: relative;
        width: fit-content;
        
        &::before {
            position: absolute;
            content: '';
            width: 38%;
            height: 1px;
            top: 50%;
            transform: translateY(-50%);
            left: 0;
            background-color: $color-main;
        }
    }

    &.__btn-watch-video {
        @include fluidFontSize(18px, 32px, 320px, 1920px, 18px);
    @include fluidLineHeight(23px, 33px, 320px, 1920px, 18px);
    letter-spacing: 0;

  
    }

    &.__presentation-info {
        margin-bottom: 10px;
        position: relative;

        @media (max-width: 767px) {
            margin-left: 30px;
        }

        &::before {
            position: absolute;
            content: '';
            background: url('../img/error.svg') center center no-repeat;
            background-size: cover;
            width: 25px;
            height: 25px;
            left: -45px;
            top: 50%;
            transform: translateY(-50%);

                @media (max-width: 767px) {
                    left: -35px;
                }
        }
    }

    &.__coordinator {
        margin-bottom: 38px;

        @media (max-width: 1199px) {
            margin-bottom: 24px;
                
            }

        @media (max-width: 767px) {
        margin-bottom: 24px;
            
        }
    }

    &.__main-sub-decor {
       
    }


    &.__secondary-colored {
        color: $color-secondary;

        .secondary-title-text__indent { 
            padding-left: 167px;
            position: relative;

            @media (max-width: 991px) {
                padding-left: 91px;
             }

             &.__our-team-videos {
                
               
                @media (min-width: 768px)  {
                    padding-left: 284px;
                 }

                 @media (max-width: 1440px) {
                    padding-left: 23%;

                }
                @media (max-width: 991px)  {
                    padding-left: 0;
                  
                 }
            }

            &.__video-reviews {
                @media (max-width: 991px)  {
                    padding-left: 100px;
                  
                 }
            }

            &.__gallery {
                @media (min-width: 768px) {
                    padding-left: 334px;
                 }

                @media (max-width: 767px) {
                    padding-left: 167px;
                 }

                 @media (max-width: 474px) {
                    padding-left: 120px;
                 }
            }
        }

    }

    &.__card-item-title-indent {
        margin-bottom: 23px;
    }

    &.__our-team-videos {
        margin-bottom: 5px;
        @include fluidFontSize(30px, 30px, 320px, 1920px, 18px);
        @include fluidLineHeight(34px, 34px, 320px, 1920px, 18px);

        @media (max-width: 1440px) {
            margin-bottom: 3%;
        }

        @media (max-width: 991px) {
            display: flex;
            justify-content: center;
            margin-bottom: 25px;
            // left: 50%;
            // transform: translateX(-50%);
        }

    }
}

