.difficulty {
    &.__decor {
        position: relative;
        padding-left: 25px;
        margin-left: 11px;
        @include fluidFontSize(13px, 20px, 320px, 1920px, 18px);
        @include fluidLineHeight(18px, 23px, 320px, 1920px, 18px);
        @include font-main-medium;

        &::before {
            position: absolute;
            content: '';
            top: 50%;
            transform: translateY(-50%);
            left: -13px;
            width: 30px;
            height: 30px;
            background: url('../img/happy-face.svg') center center no-repeat;
            background-size: cover;

            @media (max-width: 767px) {
                width: 20px;
                height: 20px;
                left: 0;
            }
        }
    }
}

.route-info {
    &.__column {
        width: 100%;
        max-width: 240px;

      
    }
    &.__column1 {
        @media (max-width: 767px) {
            display: none;
        }
    }
    &.__column2 {
        margin-right: 15%;

        @media (max-width: 1440px) {
            margin-right: 5%;
        }

        @media (max-width: 991px) {
            margin-right: 0;
            margin-left: 2%;
        }

        @media (max-width: 767px) {
            display: none;
        }
    }
    &__item {
        display: flex;
        flex-direction: column;

        &.__difficulty {
            @media (max-width: 767px) {
                flex-direction: row;
            }
        }

       

        &-title {
            @include fluidFontSize(18px, 20px, 320px, 1920px, 18px);
            @include fluidLineHeight(23px, 23px, 320px, 1920px, 18px);
            @include font-main-medium;
            margin-bottom: 10px;
            overflow-wrap: break-word;

        }

        &.__route-info-indent {
            margin-bottom: 40px;

            @media (max-width: 1024px)  {
                margin-bottom: 9%;
            }

            @media (max-width: 767px)  {
                margin-bottom: 13px;
             }
        }

        &-text {
            display: flex;
            flex-direction: column;
            @include font-main-light;
            @include fluidFontSize(13px, 18px, 320px, 1920px, 18px);
            @include fluidLineHeight(17px, 23px, 320px, 1920px, 18px);
            overflow-wrap: break-word;

            @media (max-width: 991px) {
                max-width: 200px;
            }

            .text-colored {
                color: rgba(51,51,51,0.5);
                text-decoration: underline;
                @include fluidFontSize(10px, 14px, 320px, 1920px, 18px);
                @include fluidLineHeight(12px, 17px, 320px, 1920px, 18px);
            }

            &.__small-size {
            @include font-main-regular;
            @include fluidFontSize(13px, 14px, 320px, 1920px, 18px);
            @include fluidLineHeight(18px, 18px, 320px, 1920px, 18px);

            @media (max-width: 767px) {
                @include font-main-light;
            }
            }
        }
    }

    &__columns-wrapper {
        display: flex;
        justify-content: space-between;
    }

}

.top-row {
    display: flex;
    &.__routes-info {
        position: relative;
        justify-content: space-between;
        padding-right: 15%;

        @media (max-width: 1440px) {
            padding-right: 3%;
        }


        @media (max-width: 991px) {
            padding-right: 0;
        }


    }

    &.__presentation-info {
        flex-direction: column;
        margin-bottom: 20px;
    }

    &__item {
        display: flex;
        width: 100%;
        max-width: 240px;

        &.__rating {
            @media (max-width: 767px) {
                max-width: 170px;
            }

            @media (max-width: 374px) {
                flex-direction: column;
                align-items: flex-end;
            }
        }

        &-title {
            @include fluidFontSize(18px, 20px, 320px, 1920px, 18px);
            @include fluidLineHeight(23px, 23px, 320px, 1920px, 18px);
            @include font-main-medium;
            margin-right: 8px;
            overflow-wrap: anywhere;
            width: 50%;
        }

        &.__route-info-indent {
            margin-bottom: 45px;

            @media (max-width: 1024px) {
                margin-bottom: 6%;
            }

            @media (max-width: 767px) {
                margin-top: 20px;

                margin-left: 5%;
            }

            @media (max-width: 374px) {
                margin-top: 20px;

                margin-left: 1%;
            }
        }
    }
} 

.block-text {
    @include fluidFontSize(18px, 20px, 320px, 1920px, 18px);
    @include fluidLineHeight(22px, 22px, 320px, 1920px, 18px);
    @include font-main-light;
