.form-block__checkbox-wrapper {
    margin-bottom: 20px;
  }
  

  .form-block .check__input {
    position: absolute;
    -webkit-appearance: none;
    transition: .5s;
    appearance: none;
}


.form-block .check__box {
    position: absolute;
    width: 27px;
    height: 27px;
    margin-top: 9px;
    border-radius: $border-radius;
    // border-color: $color-intro;
    box-shadow: 0 0 0 0.05em #777;
    transition: .5s;
}

.form-block .check__input:checked+.check__box {
    // box-shadow: 0 0 0 0.05em #000;
}

.form-block .check__box:before {
    position: absolute;
    content: "";
    background-image: url(../img/input-check.svg);
    background-position: top;
    background-repeat: no-repeat;
    width: 100%;
    height: 100%;
    top: 5px;
    opacity: 0;
    transition: .5s;
}

.form-block .check__input:checked+.check__box:before {
    opacity: 1;
}

.form-block .check {
    padding-left: 47px;
    padding-top: 9px;
    position: relative;
    transition: .5s;
    font-size: 12px;
    line-height: 17px;
    color: #777;
}

