.header {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;

    &-inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 47px;

        @media (max-width: 767px) {
            margin-top: 36px;
        } 

        @media (max-width: 579px) {
            margin-top: 20px;
        } 
    }
}

.nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
    color: $color-intro;
    max-width: 965px;

    &.__header {

       
        @media (max-width: 1679px) {
            max-width: 830px;
        }

        @media (max-width: 1439px) {
            max-width: 733px;
        }
        @media (max-width: 1280px) {
            max-width: 708px;
        }

        @media (max-width: 1279px) {
            display: none;
        }
    }



    &__link {
        color: inherit;
        position: relative;
        transition: $transition;
        text-transform: uppercase;
        @include fluidFontSize(9px, 14px, 768px, 1920px, 18px);
        @include fluidLineHeight(12px, 17px, 768px, 1920px, 18px);
        letter-spacing: 2px;
        margin-right: 10px;

        &:last-child {
            margin-right: 0;
        }

        &:before {
            position: absolute;
            transition: $transition;
            content: '';
            width: 100%;
            height: 1px;
            bottom: -3px;
            background-color: $color-secondary;
            opacity: 0;
        }


        &:hover {
            &:before {
                opacity: 1;
            }
        }

        
    }
}

.phone-number {
    
    &.__header {
        @media (min-width: 768px) {
            margin-left: 5%;
            }

            @media (min-width: 1365px) {
                margin-left: 2%;
                }
    }

    &.__default {
        @media (min-width: 768px) {
            margin-left: 38px;
            
            }
    }
    &.__indent {
        margin-bottom: 21px;

        @media (max-width: 767px) {
            margin-left: 27px;
            margin-bottom: 12px;
        }
    }

    &__link {
        @include fluidFontSize(18px, 18px, 320px, 1920px, 18px);
        @include fluidLineHeight(23px, 23px, 320px, 1920px, 18px);
        @include font-main-light;
        position: relative;

        &.__header {
            color: $color-intro;

            @media (max-width: 767px) {
                margin-left: 41px;
                
                }

            &:before {
                position: absolute;
                content: '';
                background: url('../../img/phone.svg') center no-repeat;
                background-size: cover;
                top: 50%;
                transform: translateY(-50%);
                left: -38px;
                width: 26px;
                height: 26px;
            }
        }
        
        &.__default {
            color: $color-main;
            @include fluidFontSize(13px, 18px, 320px, 1920px, 18px);
        @include fluidLineHeight(17px, 23px, 320px, 1920px, 18px);

            &:before {
                position: absolute;
                content: '';
                background: url('../../img/phone-black.svg') center no-repeat;
                background-size: cover;
                top: 50%;
                transform: translateY(-50%);
                left: -38px;
                width: 26px;
                height: 26px;

                @media (max-width: 767px) {
                    width: 20px;
                height: 20px;
                left: -27px;
                }
            }
    
        }

       
    }

   
}


.hamburger-menu {

    @media (min-width: 1280px) {
        display: none;
    }
}
#menu__toggle {
    opacity: 0;
}

.menu__btn {
    display: flex;
    align-items: center;
    position: absolute;
    top: 34px;
    right: 6%;
    width: 26px;
    height: 26px;
    cursor: pointer;
    z-index: 5;

    @media (max-width: 579px) {
        top: 20px;
    }
}



.hamburger-menu input:checked ~ .menu__btn {
    z-index: 101;
    position: fixed;
}



.hamburger-menu input:checked ~ .menu__btn > span {
    transform: rotate(45deg);
    background: $color-main;

}

.hamburger-menu input:checked ~ .menu__btn > span::before {
    top: 0;
    transform: rotate(0);
    background: $color-main;

}

.hamburger-menu input:checked ~ .menu__btn > span::after {
    top: 0;
    transform: rotate(90deg);
    background: $color-main;

}

.menu__btn > span,
.menu__btn > span::before, 
.menu__btn > span::after {
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    background: $color-intro;
    transition-duration: .25s;
}

.menu__btn > span::before {
    content: '';
    top: -8px;
  }
  .menu__btn > span::after {
    content: '';
    top: 8px;
  }

.menu__box {
    display: block;
    position: fixed;
    visibility: hidden;
    top: 0;
    right: 0;
    opacity: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    margin: 0;
    transition: 0.5s;
    list-style: none;
    z-index: 4;
    // box-shadow: 1px 0px 6px rgb(0 0 0 / 20%);
    background-color: rgba(0, 0, 0, 0.5);
    transition-duration: .25s;
}

.hamburger-menu input:checked ~ .menu__box {
    visibility: visible;
    right: 0;
    opacity: 1;
    z-index: 100;
}

.menu__box-inner {
    position: fixed;
    display: block;
    right: -100%;
    width: 240px;
    height: 100%;
    overflow: auto;
    margin: 0 0 0 auto;
    transition: 0.5s;
    list-style: none;
    z-index: 4;
    background-color: white;
    // box-shadow: 1px 0px 6px rgb(0 0 0 / 20%);
    transition-duration: .25s;
}

.hamburger-menu input:checked ~ .menu__box .menu__box-inner {
    right: 0;
    z-index: 101;
}

.menu__box-list {
    margin-bottom: 30px;
    padding-top: 80px;
}

.menu__item {
    display: block;
    padding: 12px 24px;
    color: #333;
    font-family: 'Roboto', sans-serif;
    font-family: 'Gilroy-Bold', sans-serif;
    font-size: 16px;
    line-height: 19px;
    text-transform: uppercase;
    transition-duration: .25s;
}

